"use client";

import { useState, useRef, ReactNode } from 'react';
import { DraggableItem } from '@/components/atoms/DraggableItem';
import DropZone from '@/components/atoms/DropZone';
import GameHeader from '@/components/molecules/GameHeader';
import { useToast } from "@/hooks/use-toast";

interface GameItem {
  id: string;
  name: string;
  category: string;
  icon: string;
  color: string;
}

interface GameSet {
  id: string;
  name: string;
  color: string;
  description: string;
  icon: any;
}

interface GameConfig {
  title: string;
  description: string;
  backgroundGradient: string;
  headerIcon: any;
  setsTitle: string;
  setsIcon: any;
  items: GameItem[];
  sets: GameSet[];
  successMessages: {
    correct: string;
    setComplete: string;
    gameComplete: string;
    incorrect: string;
  };
  scoring: {
    correctPoints: number;
    bonusPoints?: number;
  };
}

interface GamesTemplateProps {
  config: GameConfig;
  onGameComplete?: (score: number) => void;
  customHeader?: ReactNode;
  customSuccessScreen?: (score: number) => ReactNode;
}

const GamesTemplate: React.FC<GamesTemplateProps> = ({
  config,
  onGameComplete,
  customHeader,
  customSuccessScreen,
}) => {
  const { toast } = useToast();
  
  const [items, setItems] = useState(config.items);
  const [score, setScore] = useState(0);
  const [completedSets, setCompletedSets] = useState<string[]>([]);
  const dragItem = useRef<GameItem | null>(null);

  const handleDragStart = (item: GameItem) => {
    dragItem.current = item;
    console.log('Iniciando arrastre:', item.name);
  };

  const handleDrop = (setId: string) => {
    if (!dragItem.current) return;

    const item = dragItem.current;
    console.log('Soltando:', item.name, 'en conjunto:', setId);

    if (item.category === setId) {
      // Clasificaci√≥n correcta
      setItems(prev => prev.filter(i => i.id !== item.id));
      setScore(prev => prev + config.scoring.correctPoints);
      
      toast({
        title: config.successMessages.correct,
        description: `${item.name} pertenece a ${config.sets.find(s => s.id === setId)?.name}`,
        duration: 2000,
      });

      // Verificar si el conjunto est√° completo
      const remainingItemsInSet = items.filter(i => i.category === setId && i.id !== item.id);
      if (remainingItemsInSet.length === 0) {
        setCompletedSets(prev => [...prev, setId]);
        const newCompletedCount = completedSets.length + 1;
        
        // Bonus por completar conjunto
        if (config.scoring.bonusPoints) {
          setScore(prev => prev + config.scoring.bonusPoints!);
        }
        
        toast({
          title: config.successMessages.setComplete,
          description: `Conjunto ${newCompletedCount} de ${config.sets.length} completado`,
          duration: 3000,
        });
      }
    } else {
      // Clasificaci√≥n incorrecta
      toast({
        title: config.successMessages.incorrect,
        description: "Intenta con otro conjunto",
        duration: 2000,
        variant: "destructive"
      });
    }

    dragItem.current = null;
  };

  const isGameComplete = completedSets.length === config.sets.length;

  // Llamar callback cuando se complete el juego
  if (isGameComplete && onGameComplete) {
    onGameComplete(score);
  }

  const defaultSuccessScreen = (finalScore: number) => (
    <div className="text-center py-16 gentle-float">
      <div className="bg-white rounded-3xl p-8 shadow-2xl border border-green-200">
        <div className="text-6xl mb-4">üéâ‚ú®</div>
        <h2 className="text-4xl font-bold text-green-600 mb-4">
          {config.successMessages.gameComplete}
        </h2>
        <p className="text-xl text-gray-600 mb-6">
          Has completado todas las clasificaciones correctamente
        </p>
        <div className="text-3xl font-bold text-blue-600">
          Puntuaci√≥n Final: {finalScore} puntos
        </div>
      </div>
    </div>
  );

  return (
    <div className={`min-h-screen ${config.backgroundGradient} p-4`}>
      <div className="max-w-6xl mx-auto">
        {customHeader || (
          <GameHeader 
            score={score} 
            completedSets={completedSets.length} 
            totalSets={config.sets.length} 
          />
        )}
        
        {isGameComplete ? (
          customSuccessScreen ? customSuccessScreen(score) : defaultSuccessScreen(score)
        ) : (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {/* √Årea de elementos arrastrables */}
            <div className="space-y-6">
              <div className="bg-white rounded-2xl p-6 shadow-lg border border-gray-200">
                <h2 className="text-2xl font-bold text-gray-800 mb-4 flex items-center gap-2">
                  <config.headerIcon className="text-blue-500" />
                  {config.title}
                </h2>
                <p className="text-gray-600 mb-6">
                  {config.description}
                </p>
                <div className="grid grid-cols-2 sm:grid-cols-3 gap-4">
                  {items.map((item) => (
                    <DraggableItem
                      key={item.id}
                      item={item}
                      onDragStart={handleDragStart}
                    />
                  ))}
                </div>
              </div>
            </div>

            {/* √Årea de conjuntos */}
            <div className="space-y-4">
              <h2 className="text-2xl font-bold text-gray-800 flex items-center gap-2">
                <config.setsIcon className="text-green-500" />
                {config.setsTitle}
              </h2>
              {config.sets.map((set) => (
                <DropZone
                  key={set.id}
                  set={set}
                  onDrop={handleDrop}
                  isCompleted={completedSets.includes(set.id)}
                  itemCount={items.filter(item => item.category === set.id).length}
                />
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default GamesTemplate;